.App {
  min-height: 100vh;
  text-align: center;
}

.App-logo {
  height: 8vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.dot {
  padding: 0px 10px;
  display: inline-block;
}

.dot-red {
  height: 10px;
  width: 10px;
  background-color: rgba(255, 0, 0, 0.815);
  border-radius: 50%;
  display: inline-block;
}

.dot-green {
  height: 10px;
  width: 10px;
  background-color: rgb(72, 255, 0);
  border-radius: 50%;
  display: inline-block;
}

.title {
  font-size: 20px;
  color: white;
}

.detail {
  text-align: left;
}

:root {
  --input-padding-x: 1.5rem;
  --input-padding-y: 0.75rem;
}

html {
  position: relative;
  min-height: 100%;
}

body {
  min-height: 100vh;
  background: linear-gradient(to top, #0062e6, #33aeff);
}

.root {
  min-height: 100vh;
}

header {
  min-height: 10vh;
  margin: 20px 0px;
}

main {
  flex: 1;
}

footer {
  bottom: 0;
  width: 100%;
  min-height: 10vh;
  margin: 20px 0px;
}

.card-signin {
  border: 0;
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);
}

.card-signin .card-title {
  margin-bottom: 2rem;
  font-weight: 300;
  font-size: 1.5rem;
}

.card-signin .card-body {
  padding: 2rem;
}

.form-signin {
  width: 100%;
}

/* Fallback for Edge
-------------------------------------------------- */

@supports (-ms-ime-align: auto) {
  .form-label-group > label {
    display: none;
  }
  .form-label-group input::-ms-input-placeholder {
    color: #777;
  }
}

/* Fallback for IE
-------------------------------------------------- */

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .form-label-group > label {
    display: none;
  }
  .form-label-group input:-ms-input-placeholder {
    color: #777;
  }
}
